@import url(https://fonts.googleapis.com/css2?family=Cutive+Mono&display=swap);
.nav-link {
    opacity: 50% !important;
}

.nav-link:hover {
    color: rgb(116, 102, 52) !important;
    opacity: 100% !important;
}

.home, .active {
    color: rgb(116, 102, 52) !important;
    opacity: 100% !important;
}


.footer {
    display: block;
    text-align: center;
    font-size: 0.75em;
    bottom:0;
    width: 100%;
    height: 30px;
    position: fixed;
}

.footer a, a:hover, a:focus, a:active {
      text-decoration: none;
      color: inherit;
 }
.wrapper {
  font-family: 'Cutive Mono', monospace;
  position: relative;
  width: 100%;
  height: 100%;
}

.wrapper__img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100%;
  min-width: 1024px;
  z-index: -10;
}

@media (max-width: 1024px) { 
  .wrapper__img {
    left: 50%;
    margin-left: -512px;   /* 50% */
  }
}

@media (max-width: 768px) { 
  .wrapper__img {
    position: fixed;
  }
}
.loader {
    margin: 0 auto;
}
